@import url('https://fonts.googleapis.com/css2?family=Gothic+A1&display=swap');

* {
    font-family: 'Gothic A1', sans-serif;
}

h1 {
    text-align: center;
    font-size: 1.5rem;
}
.main-content {
    /* background-color: red; */
    display:flex;
    justify-content:space-around;
    align-items: center;
    margin-top: 70px;
}

product {
    max-width: 500px;
    /* background-color: green; */
    padding: 10px;
    text-align: center;

}

h2 {
    color: green;
    margin: 0 0 20px;
    font-size: 1.5rem;
    border-bottom: 3px double green;
    text-align: center;
}

select {
    font-size: 0.9rem;
    padding: 0.5em;
    margin: 0 40px;
}

.ud-number {
font-weight: bold;
color: green;
}

img {
    width: 350px;
    height: 350px;
    margin: 0 auto;
    display: block;
    margin-bottom: 20px;
    object-fit: contain; /*prevent squeezing on small-screen devices*/
}
.ud-title{
    color: green;
}
.ud-paragraph {
text-align: left;
font-style: italic;
}

.ud-first-heading {
padding-top: 20px;
}